<template>
    <div class="container">
        <div class="row">
            <div class="col-lg-12">
                <div class="alert alert-danger" role="alert" v-if="error">
                    <strong>Oh snap!</strong> {{error}}
                </div>
            </div>
            <div class="col-lg-12">
                <div class="alert alert-danger" role="alert" v-if="success">
                     {{success}}
                </div>
            </div>
        </div>    
        <div class="row col-md-6 col-lg-12 col-md-offset-2 custyle">
            <Contacts v-bind:contactList="contacts" v-on:del-contact="handleDelContact"/>
        </div>
    </div>
</template>

<script>
import ContactService from '../ContactService';
import Contacts from '../components/contacts'

export default {
  name: 'Home',
  components: {
    Contacts
  },
  data(){
      return{
        contacts:[],
        error : '',
        success:this.$route.params.msg,
        fname : '',
        phone : '',
        id : '',
        group_id : ''
      }
  },
  async created(){
    try {
      this.contacts = await ContactService.getContact();
    } catch (error) {
      this.error = error.message;
    }
  },
  methods: {
      // Delete Contact
      async handleDelContact(id){
          try {
              await ContactService.deleteContact(id);
              this.contacts = await ContactService.getContact();
          } catch (error) {
              this.error = error.message;
          }
      }
  },
}
</script>


