import axios from 'axios';

const url = 'http://localhost:3000/api/groups';

class GroupService{
    // Get groups
    static getGroups(){
        return new Promise(async (resolve,reject)=>{
            try {
                const res = await axios.get(url);
                const data = res.data;
                resolve(
                    data.map(group => ({
                        ...group,
                        createdAt:new Date(group.createdAt)
                    }))
                );
            } catch(err) {
                reject(err);
            }
        })
    }
}

export default GroupService;