<template>
  <div class="addContact">
    <div class="container-fluid appRoot">
      <div class="container col-lg-6">
          <h1>Add New Contact</h1>
          <div class="col-lg-12">
                <div class="alert alert-danger alert-dismissible fade show" role="alert" v-if="error.error">
                  <button type="button" class="close" data-dismiss="alert" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                  </button>
                  <strong>Holy guacamole!</strong> {{error.msg}}.
                </div>

                <div class="alert alert-success alert-dismissible fade show" role="alert" v-if="success.bool">
                  <button type="button" class="close" data-dismiss="alert" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                  </button>
                   {{success.msg}}.
                </div>
          </div>
          <div class="box">
              <form @submit="formSubmit">
                  <div class="form-group">
                      <label for="fname">First Name</label>
                      <input type="text" v-model="fname" class="form-control" id="fname" name="fname" />
                  </div>
                  <div class="form-group">
                      <label for="phone">Contat Number</label>
                      <input type="text" v-model="phone" class="form-control" id="phone" name="phone" />
                  </div>
                  <div class="from-group">
                      <label for="group_id">Contact Group</label>
                      <select class="form-control" v-model="group_id" name="group_id" id="group_id">
                          <option 
                              v-for="(group, index) in groups"
                              v-bind:item="group"
                              v-bind:index="index"
                              v-bind:key="group.gid"

                              :value="group.gid"
                          >
                            {{ group.group_name }}
                          </option>
                      </select>
                  </div>
                  <div class="clearfix">
                      <button type="submit" class="btn btn-primary btn-customs">Save</button>
                      <!-- <button type="submit" class="btn btn-primary btn-customs">Update</button> -->
                  </div>
                  
              </form>
          </div>
      </div>
    </div>
  </div>
</template>

<script>
import ContactService from '../ContactService';
import GroupService from '../GroupService';

export default {
  name: 'AddContact',
  data(){
    return{
      groups:[],
      fname:'',
      phone:'',
      group_id:'',
      error:{},
      success:{}
      
    }
  },
  async created(){
    try {
      this.groups = await GroupService.getGroups();
    } catch (error) {
      this.error = this.error = {
          error : true,
          msg:error.message
        }
    }
  },
  methods: {
    async formSubmit(e){
      e.preventDefault();
      if( this.fname === '' || this.fname == null ){
        this.error = {
          error : true,
          msg:"please fill First Name"
        }
        die();
      } 

      if( this.phone === '' || this.phone == null ){
          this.error = {
            error : true,
            msg:"please fill Contact number"
          }
          return;

      }else{
        if( isNaN(this.phone) ){
          this.error = {
              error : true,
              msg:"Please enter valid contact number, must be digit and length till 10 or 11"
          }
          return;
        }
      }

      if( this.group_id === '' || this.group_id == null  ){
        this.error = {
              error : true,
              msg:"Please select a contact group"
          }
          return;

      }

      const newContact ={
          fname: this.fname,
          phone: this.phone,
          group_id : this.group_id
      }
      
      // add to the data base
      try {
        await ContactService.insertContact(newContact);
        this.fname = '';
        this.phone = '';
        this.group_id = '';

        this.success = {
          bool : true,
          msg : "New Contact as add :)"
        }
      } catch (errors) {
        this.error = {
          error : true,
          msg : errors.message
        }
      }


      this.error = {
              error : false,
              msg:"Please enter valid contact number, must be digit and length till 10 or 11"
      }

    }
  },
}
</script>

<style scoped>
.box{
    color:#686466;
    background-color: #B1E1E9;
    padding: 1rem 1rem;
}
.btn-customs{
    margin:1rem 0;
    background-color: #839CB5;
    border: #839CB5;
    padding: .5rem 2rem;
    float: right;
}
</style>
