import axios from 'axios';

const url = 'http://localhost:3000/api/contacts/';

class ContactService{
    // Get Contact
    static getContact(){
        return new Promise(async (resolve,reject)=>{
            try {
                const res = await axios.get(url);
                const data = res.data;
                resolve(
                    data.map(contact => ({
                        ...contact,
                        createdAt:new Date(contact.createdAt)
                    }))
                );
            } catch(err) {
                reject(err);
            }
        })
    }

    static getContactSingle(id){
     
        return new Promise(async (resolve,reject)=>{
            try {
                const res = await axios.get(`${url}${id}`);
                const data = res.data;
                resolve(
                    // data
                    data.map(contact => ({
                        ...contact,
                        createdAt:new Date(contact.createdAt)
                    }))
                );
            } catch(err) {
                reject(err);
            }
        })
    }

    // Create Contact
    static insertContact(data){
        const {fname, phone, group_id} = data;
        return axios.post(url, {
            fname,
            phone,
            group_id
        });
    }

    // Update Contact
    static updateContact(data){
        const {id,fname, phone, group_id} = data;
        return axios.put(`${url}`, {
            id,
            fname,
            phone,
            group_id
        });
    }

    // Delete Contact
    static deleteContact(id){
        return axios.delete(`${url}${id}`);
    }
}

export default ContactService;